.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 20px;
  padding: 1% 5%;
  display: flex;
}

.image-item {
  width: 100%;
  height: 80%;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 5px 5px 0 0;
  display: block;
}

.info {
  height: 20%;
  text-align: center;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.photo-card {
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: 5px;
  flex-basis: 20%;
  transition: all .3s;
  display: block;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.photo-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, .2);
}

.load-more, .sorry-end {
  display: none;
}

.visible {
  display: block;
}

.load-more {
  width: 200px;
  height: 50px;
  background-color: #add8e6;
  border: 1px dashed #add8e6;
  border-radius: 60px;
  margin-bottom: 2%;
  margin-left: auto;
  margin-right: auto;
  transition: all .3s;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.load-more:hover {
  background-color: #e0ffff;
  border-color: #e0ffff;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .2);
}

.sorry-end {
  text-align: center;
  font-size: larger;
  font-weight: bold;
}

